import React from "react";
import AppBar from "@mui/material/AppBar";
import Toolbar from "@mui/material/Toolbar";
import { Link as RouterLink } from "react-router-dom";
import { appBarStyles, toolbarStyles, buttonStyles } from "../styles/styles"; // Import des styles
import Button from "@mui/material/Button"; // Import de Button depuis MUI

const Header = () => (
  <AppBar position="fixed" sx={appBarStyles}>
    <Toolbar sx={toolbarStyles}>
      <Button sx={buttonStyles} component={RouterLink} to="/">
        Home
      </Button>
      <Button sx={buttonStyles} component={RouterLink} to="/skills">
        Skills
      </Button>
      <Button sx={buttonStyles} component={RouterLink} to="/projects">
        Projects
      </Button>
      <Button sx={buttonStyles} component={RouterLink} to="/contact">
        Contact
      </Button>
      <Button sx={buttonStyles} component={RouterLink} to="/about">
        About
      </Button>
    </Toolbar>
  </AppBar>
);

export default Header;
