// src/components/AnimatedPage.js
import React from 'react';
import { motion } from 'framer-motion';
import { motionStyles } from './animation';

const AnimatedPage = ({ children, key }) => {
  return (
    <motion.div key={key} initial={motionStyles.initial} animate={motionStyles.animate} exit={motionStyles.exit} transition={motionStyles.transition}>
      {children}
    </motion.div>
  );
};

export default AnimatedPage;
