// src/components/Skills.js
import React from "react";
import SkillCarousel from "./skillCarousel";
import { FaReact, FaNodeJs, FaPython, FaJs } from "react-icons/fa";

const skillsData = {
  "Web Development": [
    { name: "React" },
    { name: "Node.js" },
    { name: "JavaScript" },
  ],
  "Software Development": [
    { name: "React" },
    { name: "Node.js" },
    { name: "JavaScript" },
  ],
  "Virtualization & Project Management": [
    { name: "React" },
    { name: "Node.js" },
    { name: "JavaScript" },
  ],
  Cybersecurity: [
    { name: "React" },
    { name: "Node.js" },
    { name: "JavaScript" },
    { name: "Cybersecurity" },
  ],
};

const Skills = () => {
  return (
    <div>
      {Object.entries(skillsData).map(([title, skills]) => (
        <SkillCarousel key={title} title={title} skills={skills} />
      ))}
    </div>
  );
};

export default Skills;
