// src/components/SkillCarousel.js
import React from 'react';
import Slider from 'react-slick';
import { Box, Typography, Card, CardContent } from '@mui/material';

const SkillCarousel = ({ title, skills }) => {
  
const settings = {
  dots: false,
  infinite: true,
  speed: 1000, // Durée de la transition entre les diapositives en millisecondes
  slidesToShow: 3, // Nombre de diapositives visibles simultanément
  slidesToScroll: 1, // Nombre de diapositives à faire défiler à chaque fois
  arrows: true, // Affichage des flèches de navigation
  autoplay: true, // Défilement automatique
  autoplaySpeed: 1000, // Vitesse du défilement automatique en millisecondes
  cssEase: 'linear', // Effet de transition linéaire pour un défilement fluide
  

};


const carouselBox = () => (
  <Box
    display="flex"
    flexDirection="column"
    alignItems="center"
   
 >
    {/* Contenu ici */}
  </Box>
);


  return (
    <Box >
      <Typography  class ="Subheading">
        {title}
      </Typography>
      <Slider {...settings} >
        {skills.map((skill, index) => (
          <Card key={index} sx={{
            backgroundColor: 'rgba(58, 197, 231, 0.5)', // Couleur de fond bleu clair
            color: 'white',

           
          }}>
            <CardContent   >
              <carouselBox >
                {skill.icon}
                <Typography class="Body1">
                  {skill.name}
                </Typography>
              </carouselBox>
            </CardContent>
          </Card>
        ))}
      </Slider>
    </Box>
  );
};

export default SkillCarousel;
